.nav-container {
  backdrop-filter: blur(10px);
  position: fixed;
  width: 100vw;
  z-index: 10;
}
.nav-contents {
  display: flex;
  gap: 1em;
  font-size: 20px;
  margin: 0px;
  padding: 10px;
  color: white;
  margin-left: 20px;
}
.size1 {
  flex: 1;
  transition: all ease 0.3s;
  border: solid;
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: transparent;
  border-radius: 10%;
  padding: 3px;
}
.size1:hover {
  cursor: pointer;
  border-color: white;
  padding: 5px;
}
.size2 {
  flex: 1;
  transition: all ease 0.3s;
  border: solid;
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: transparent;
  border-radius: 10%;
  padding: 3px;
}
.size2:hover {
  cursor: pointer;
  border-color: white;
  padding: 5px;
}
.size3 {
  flex: 10;
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
}
.buttons {
  height: 30px;
  width: 30px;
  margin: 10px;
  border: solid;
  border-radius: 50%;
  margin-top: 0px;
  transition: all ease 0.5s;
}
.buttons:hover {
  color: #000;
  height: 40px;
  width: 40px;
}
ul {
  list-style-type: none;
}
Link {
  text-decoration: none;
  color: inherit;
}
