.items-container {
  color: #e8af30;
  background-color: #3361ac;
  display: flex;
  align-items: center;

  flex-direction: column;
}
.block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.item {
  background-color: #e7e6dd;
  padding: 10px;
  width: 300px;
  border-radius: 50%;
  display: flex;
  font-size: 25px;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 20px;
  transition: ease all 0.5s;
  cursor: pointer;
}
.item:hover {
  background: #e8c766;
  color: #0f2043;
}
