.one-container {
  background: #0f2043;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.conten {
  background: #e8af30;
  color: #0f2043;
  height: 70vh;
  width: 50vw;
  padding: 10px;
  border-radius: 5%;
  overflow: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.conten::-webkit-scrollbar {
  display: none;
}

.content-header {
  text-align: center;
  text-transform: uppercase;
}
.content-writing {
  font-size: 18px;
}
