.home-container {
  display: flex;
  padding: 40px;
  padding-bottom: 120px;
  padding-top: 100px;
  height: 80vh;
}
.line1 {
  color: white;
  backdrop-filter: blur(10px);
  width: 400px;
  font-size: 30px;
  margin-left: 100px;
  border-radius: 10%;
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 10px 5px 0px #000000;
  transition: all ease 0.5s;
}
.line1:hover {
  transition: all ease 0.5s;
  box-shadow: 0px 0px 0px 0px #000000;
  color: yellow;
  background: #000;
}
.home2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.vamp {
  background: url("../1.png");
  background-size: 120px;
  height: 120px;
  width: 100px;
  transition: ease all 0.5s;
}
.vamp:hover {
  background: url("../vamp-real.png");
  background-size: 500px;
  height: 500px;
  width: 200px;
  margin-right: -100px;
  margin-bottom: -150px;
  margin-top: -200px;
}
.prin {
  background: url("../princess.png");
  background-size: 150px;
  height: 150px;
  width: 100px;
  transition: ease all 0.5s;
}
.prin:hover {
  background: url("../queen-real.png");
  background-size: 400px;
  height: 400px;
  width: 200px;
  margin-right: -50px;
  margin-bottom: -200px;
  margin-top: -100px;
}
.vampprin {
  display: flex;
}
.line2 {
  color: rgb(255, 238, 4);
  font-size: 20px;
  background: #000;
  padding: 10px;
}
